
.wrapper{
    display: flex;
    align-items: center;
    padding: 20px;
    border-bottom: 1px dashed var(--yellow-theme-main-color);

    h2{
       font-size: 36px;
       font-weight: 700px;
       letter-spacing: 1.5px;
       line-height: 1rem;
       text-transform: capitalize;
       color: var(--yellow-theme-main-color);
       display: flex;
       flex-grow: 1;
    }
    span{
        svg{
            color: var(--yellow-theme-main-color);
        }
    }
}